:root{
  margin: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  min-width: 10vw;
  max-width: 100vw;

}

.App h2{
  margin: 10px auto;
}

.App-link {
  color: #61dafb;
}

.App .nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  overflow: hidden;
  border: 1px dotted red;
  padding: 10px;
}

.nav li{
  text-decoration: none;
  list-style: none;
  padding: 0 1rem;
}

.nav li:hover{
  transform: scale(1.2) rotate(-5deg);
  transition: 1s;
}

.nav li a{
  text-decoration: none;
  list-style: none;
  color: aliceblue;
  text-shadow: -3px 3px 5px red;
}

.nav li a:visited {
  color: rgb(41, 123, 196);
}

.nav li a:hover {
  text-shadow: -5px 5px 7px red;
  transition: 1s;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
